import React from "react";
import "./intro.css";
import me from "../../img/me.jpg";
const Intro = () => {
  return (
    <div className="i">
      <div className="i-left">
        <div className="i-left-wrapper">
          <h2 className="i-intro">Hello, My name is</h2>
          <h1 className="i-name">Shamit</h1>
          <div className="i-title">
            <div className="i-title-wrapper">
              <div className="i-title-item">Web Developer</div>
              <div className="i-title-item">Writer</div>
              <div className="i-title-item">Fitness enthusiast</div>
              <div className="i-title-item">photography</div>
            </div>
          </div>
          <p className="i-desc">
            I design and devlope websites,simple and usable and accessible
            modern website to get result
          </p>
        </div>
      </div>
      <div className="i-right"> </div>
      <img src={me} alt="" className="i-img" />
    </div>
  );
};
export default Intro;
